*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body{
  font-family: 'Righteous', sans-serif;
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;

  
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.banner{
  margin-top: 25px;
  
}
.banner>img{
  max-width: 300px;
}

.card{
  width: 100px;
  height: 100px;
  position: relative;

  -webkit-transform-style: preserve-3d;
  -webkit-transition: transform .5s;
  transform-style: preserve-3d;
  transition: transform .5s;
}

.card_back, .card_front, .matched{
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 2px 2px 7px #000000;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.icon{
  width:80px;
  height: 80px;
}

.flip{
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

/*match*/
@keyframes animationMatch{
  0%{background-color: rgb(241, 212, 147);transform: rotateY(180deg);}
  10%{background-color: rgb(241, 212, 147);transform: rotateY(90deg);}
  20%{background-color: rgb(245, 60, 60);transform: rotateY(180deg);}
  30%{background-color: rgb(245, 60, 60);transform: rotateY(90deg);}
  40%{background-color: red;              transform: rotateY(180deg);}
  50%{background-color: red;              transform: rotateY(90deg);}
  60%{background-color: rgb(247, 169, 0);transform: rotateY(180deg);}
  70%{background-color: rgb(247, 169, 0);transform: rotateY(90deg);}
  80%{background-color: rgb(255, 255, 255);transform: rotateY(90deg);}
  85%{background-color: rgb(255, 255, 255);transform: rotateY(180deg);}
  90%{background-color: rgb(255, 255, 255);transform: rotateY(90deg);}
  95%{background-color: rgb(255, 255, 255);transform: rotateY(180deg);}
  97%{background-color: rgb(255, 255, 255);transform: rotateY(90deg);}
  100%{background-color: rgb(247, 111, 0); transform: rotateY(180deg);}
  }

.matched{
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  background-color: rgb(247, 70, 0);
  animation-name: animationMatch;
  animation-duration: 2s;
  animation-timing-function: ease-in;
}
.matched:hover{background-color: rgb(248, 158, 83);}

.card_front{
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  background-color: #101c2c;
}
.card_front:hover{background-color: #1c2f47;;}

.card_back{
  color: #101c2c;
  background-color: darkslateblue;
  font-size: 30px;

  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;

  cursor: pointer;
}
.card_back:hover{background-color: rgb(87, 79, 143);}

@keyframes chromaLetter{
  0%{color: rgb(0, 255, 21);}
  25%{color: yellow;}
  50%{color: rgb(255, 0, 0);}
  75%{color: rgb(0, 60, 255);}
  100%{color: purple;}
}

@keyframes fadeIN{
  0%{background-color: rgba(0, 0, 0, 0.25); top: 100%;}
  25%{background-color: rgba(0, 0, 0, 0.45);}
  50%{background-color: rgba(0, 0, 0, 0.65);}
  75%{background-color: rgba(0, 0, 0, 0.85);}
  100%{background-color: rgba(0, 0, 0, 0.9); top: 0%;}
}

.fadeInAnimation{
  animation-name: fadeIN;
  animation-duration: 5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
}

#gameOver{
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top:0;
  left:0;


}
.gameOverLetters{
  font-size: 150px;
  animation-name: chromaLetter;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

#restart{
  padding: 20px;
  font-size: 20px;
  background-color: darkslateblue;
  border: none;
  border-radius:10px;
  margin-top:10px;

  cursor: pointer;
}
#restart:hover{background-color: rgb(87, 79, 143); padding: 25px; font-weight: bold; color:white}

#gameBoard{
  max-width: 700px;
  display: grid;
  grid-template: 1fr 1fr 1fr 1fr 1fr / 1fr 1fr 1fr 1fr;
  gap:15px
}

@media only screen and (max-width: 700px) {
  .game-board{
      max-width: 400px;

      grid-template: 1fr 1fr 1fr 1fr 1fr  / 1fr 1fr 1fr 1fr;
      gap: 20px;
    
  }
  .card{
      width: 80px;
      height: 80px;}
  .icon
{
  width: 40px;
  height: 40px;
}
.card_front,.card_back{
border-radius:10px;

}
}